.logo {
    width: 500px;
    height: 100px;
    background-image: url('./7l.png');
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    left: 30px;
    top: -30px;
}

@media (max-width:1400px) {
    .logo {
        width: 350px;
        height: 65px;
    }

}